import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '@app/core/authentication/authentication.service';
import { ReportService } from '@app/core/services/serviceIndex';
import { AdminSideMenu, EssentialRoute } from '@app/enum/app_enum';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-progress-report',
  templateUrl: './progress-report.component.html',
  styleUrls: ['./progress-report.component.css']
})
export class ProgressReportComponent implements OnInit {

  public routeLists: any = {
    routeName: [AdminSideMenu.Report],
    routeLink: []
  };
  public reportLists: any = [];
  public page: any = 1;
  public limit: any = 10;
  public direction: String = 'down';
  public searchData: String;
  public isclassEmpty: boolean = false;
  public subscription: Subscription;
  public userData:any;

  constructor(private _service: ReportService, private _router: Router,private _auth:AuthenticationService) { }

  ngOnInit(): void {
    this.userData = this._auth.currentUserValue;
    console.log(this.userData)
    this.getReportList();
  }

  getReportList() {
    this.subscription = this._service.getUsersClasses(this.page, this.limit, this.direction, this.searchData)
      .subscribe((res) => {
        console.log(res);
        if (this.direction) {
          this.isclassEmpty = res.classes.length == 0;
          this.reportLists = this.reportLists.concat(res.classes);
        }
        else if (this.searchData) {
          this.reportLists = res.classes;
          this.page = 1;
        }
        else {
          this.reportLists = res.classes;
        }
        console.log(this.reportLists)
      })
  }

  onsearch(val: String) {
    this.searchData = val;
    this.reportLists = [];
    this.getReportList();
  }

  onScroll(event: any) {
    var element = event.target;
    if (!this.searchData) {
      if (element.scrollHeight - element.scrollTop === element.clientHeight) {
        if (!this.isclassEmpty) {
          this.direction = "down";
          this.page += 1;
          this.getReportList()
        }
      }
    }
  }

  goToStudents(id: string,classId:string) {
    this._router.navigateByUrl("teacher/" + EssentialRoute.StudentByReport + "/" + id+"/"+classId+"/progress")
  }

 
  ngOnDestroy(){
    if(this.subscription)
    this.subscription.unsubscribe();
  }

}
