import { Component, OnInit } from '@angular/core';
import { ReportService } from '@app/core/services/report.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-parent-report',
  templateUrl: './parent-report.component.html',
  styleUrls: ['./parent-report.component.css']
})
export class ParentReportComponent implements OnInit {
  public tabLists: any = ['Progress Report', 'Final Report'];
  public activeIndex=0;
  public selectedUserId:any;
  public pcList:any=[];

  private subscription:Subscription;

  constructor(private _service:ReportService) { }

  ngOnInit(): void {
    this.getParentChildList();
  }

  tabChange(index: any) {
    this.activeIndex = index;
    // if (this.activeIndex == 1)
    //   this.getPaymentLists();
  }

  getParentChildList(){
    this.subscription=this._service.getParentChildList()
    .subscribe((res)=>{
      this.pcList=res;
    })
  }

  isModal:boolean=false;
  selectedtype:string="";
  onExport(userId:string){
    this.isModal=true;
    this.selectedUserId=userId;
  }

  closeModal(){
    this.isModal=false;
    this.selectedtype=null;
    this.selectedUserId=null;
  }

  onExportApi(){
    console.log(this.selectedtype);
    this._service.onExportFile(this.selectedUserId,this.selectedtype);
    this.closeModal();
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    if(this.subscription)
    this.subscription.unsubscribe();
  }
}
