import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ReportService } from '@app/core/services/report.service';
import { AuthenticationService } from '@app/core/authentication/authentication.service';

import { CommonExpands, TeacherSideMenu, EssentialRoute } from "@enum/app_enum";
import { Report } from "@model/report";
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-report-create',
  templateUrl: './report-create.component.html',
  styleUrls: ['./report-create.component.css']
})
export class ReportCreateComponent implements OnInit {
  public routeDirLists: any = {
    routeName: [TeacherSideMenu.Report],
    routeLink: [EssentialRoute.ReportList,]
  };
  public submitted: boolean;
  public reportForm: FormGroup;
  public file: any;
  public studentId: String; reportId: String;
  public subscription: Subscription;

  constructor(private formBuilder: FormBuilder, private _ser: ReportService,
    private _activeRoute: ActivatedRoute, private _router: Router, private _auth: AuthenticationService) { }

  ngOnInit(): void {
    this.getCheckData();
  }

  getCheckData() {
    this._activeRoute.params.subscribe(params => {
      console.log(params.reportId);
      this.studentId = params.studentId;
      this.reportId = params.reportId;
      this.routeDirLists.routeLink.push(EssentialRoute.StudentByReport + "/" + this.studentId);
      this.reportForm = this.formBuilder.group({
        note: ["", [Validators.required]],
        term: ["", [Validators.required]],
      });
      this.routeDirLists.routeName.push(localStorage.getItem("studentName"));
      if (!this.reportId) {
        this.routeDirLists.routeName.push(CommonExpands.Create);
      }
      else {
        this.getReportDetail();
        this.routeDirLists.routeName.push(CommonExpands.Edit);
      }
    });
  }

  getfile(file: any) {
    this.file = file;
  }

  onSubmit() {
    this.submitted = true
    if (this.reportForm.invalid || !this.file) return;
    else {

      if (this.reportId) {
        this.onEdit();
      } else {
        this.onCreate()
      }

    }
  }

  onCreate() {
    let report = new Report();
    report.note = this.reportForm.value.note;
    report.report = this.file;
    report.studentId = this.studentId;
    report.type=this.reportForm.value.term;

    console.log(report);
    this.subscription = this._ser.onCreateReport(report)
      .subscribe(() => {
        this.submitted = false;
        this.goBack();

      })
  }

  onEdit() {
    let report = new Report();
    report.note = this.reportForm.value.note;
    report.report = this.file;
    report.studentId = this.studentId;
    report._id = this.reportId
    report.type=this.reportForm.value.term;
    this.subscription = this._ser.onEditReport(report)
      .subscribe(() => {
        this.submitted = false;
        this.goBack();

      })
  }
  getReportDetail() {
    this.subscription = this._ser.getReportDetail(this.reportId)
      .subscribe((report) => {
        let tempReport = report.report;
        this.reportForm = this.formBuilder.group({
          note: [tempReport.note, [Validators.required]],
        });

      })
  }

  goBack() {
    let currentRole = this._auth.currentUserValue.role.toLowerCase();
    this._router.navigateByUrl(currentRole + "/" + EssentialRoute.StudentByReport + "/" + this.studentId);
    console.log(currentRole + "/" + EssentialRoute.StudentByReport + "/" + this.studentId)
  }
  // convenience getter for easy access to form fields
  get f() { return this.reportForm.controls; }

}
