import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from "@angular/forms";

// com lists
import { UserViewComponent } from "./user-view/user-view.component";
import { ReportTeacherComponent } from '@app/modules/report/report-teacher/report-teacher.component';
import { ReportForStudentsComponent } from './report-for-students/report-for-students.component';
import { ReportCreateComponent } from './report-create/report-create.component';

// local module import
import { Role } from "@enum/app_enum";
import { AuthGuardService } from "@core/guards/auth-guard.service";
import { BasicComponentsModule } from "@basic-components/basic-components.module";
import { ParentReportComponent } from './parent-report/parent-report.component';
import { ProgressReportComponent } from './progress-report/progress-report.component';
import { FinalReportComponent } from './final-report/final-report.component';


const routes: Routes = [
  {
    path: 'management',
    component: ReportTeacherComponent,
    canActivate: [AuthGuardService],
    data: {
      roles: [Role.Admin, Role.Teacher, Role.StudentAffair,Role.Parent],
    }
  },
  {
    path: 'students/:id/:classId/:type',
    component: ReportForStudentsComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin, Role.Teacher, Role.StudentAffair,Role.Parent] }
  },
  {
    path: 'students/create/:studentId',
    component: ReportCreateComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Teacher] }
  },
  {
    path: 'students/edit/:studentId/:reportId',
    component: ReportCreateComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Teacher] }
  },
];


@NgModule({
  declarations: [
    ReportTeacherComponent,
    ReportForStudentsComponent,
    ReportCreateComponent,
    UserViewComponent,
    ParentReportComponent,
    ProgressReportComponent,
    FinalReportComponent
  ],
  imports: [
    CommonModule,
    BasicComponentsModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule,
    FormsModule
    // PipeModule
  ],
  exports: [
  ]
})
export class ReportModule { }
