import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { ReportService } from "@core/services/serviceIndex";
import { AuthenticationService } from "@core/authentication/authentication.service";

import { TeacherSideMenu, Label, ActionLabel, Role, EssentialRoute } from "@enum/app_enum";
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-report-for-students',
  templateUrl: './report-for-students.component.html',
  styleUrls: ['./report-for-students.component.css']
})
export class ReportForStudentsComponent implements OnInit {
  public routeDirLists: any = {
    routeName: [TeacherSideMenu.Report],
    routeLink: [EssentialRoute.ReportList]
  }
  public objectLabel: any = ["assementName", "assementType", "concept", "maxScore", "socrePercent"];
  public searchData: String;
  public limit: any = 10;
  public page: any = 1;
  public isAsc: boolean = true;
  public filter: any = {
    sortColumn: this.objectLabel[0],
    sortDirection: "asc",
    index: 0
  };
  public actionLinks: any = {
    editLink: "",
    deleteLink: "reports",
    idKeyname: "reportId"
  }
  public studentId: string;
  public reportLists: any = [];
  public isLoading: boolean = false;
  public actionBtns: any = [];
  public currentRole: any;
  public role = Role.Teacher;
  public subscription: Subscription;
  public totalCount: any;
  public type: any;
  public classId: string;

  constructor(private _activeRoute: ActivatedRoute, private _auth: AuthenticationService,
    private _reportSerivce: ReportService, private _router: Router) { }

  ngOnInit(): void {
    this._activeRoute.params.subscribe(params => {
      this.studentId = params.id;
      this.type = params.type;
      this.classId = params.classId;

      console.log(this.type);
      this.actionLinks.editLink = "teacher/reports/students/edit/" + this.studentId;
      this.checkPermission();
      if (this.type == 'progress')
        this.getReportProgress();
      else this.getReportStudents();
    });
  }

  private checkPermission() {
    this.currentRole = this._auth.currentUserValue.role;
    if (this.role == this.currentRole)
      this.actionBtns = [ActionLabel.edit, ActionLabel.download, ActionLabel.delete];
    else this.actionBtns = [ActionLabel.download]
  }

  getReportProgress() {
    this.isLoading = true;
    this.subscription = this._reportSerivce.getReportProgress(this.searchData, this.page, this.limit, this.filter.sortDirection, this.filter.sortColumn, this.studentId, this.classId)
      .subscribe((res) => {
        let temp: any = {};
        this.totalCount = res.totalCount;
        this.routeDirLists.routeName.push(res.studentName);
        localStorage.setItem("studentName", res.studentName);
        temp.data = res.progresses;
        temp.tableHeader = [Label.name, 'Assessment Type', 'Concept', 'Max Score', 'Score Percent', Label.action];
        temp.actionBtns = this.actionBtns;
        temp.btnLink = this.actionLinks;
        temp.objectLabel = this.objectLabel;
        this.reportLists = temp;
        this.isLoading = false;
        this.searchData = null;
      })
  }

  getReportStudents() {
    this.isLoading = true;
    this.subscription = this._reportSerivce.getReportStudents(this.searchData, this.page, this.limit, this.filter.sortDirection, this.filter.sortColumn, this.studentId)
      .subscribe((res) => {
        let temp: any = {};
        this.totalCount = res.totalCount;
        this.routeDirLists.routeName.push(res.studentName);
        localStorage.setItem("studentName", res.studentName);
        temp.data = res.reports;
        temp.tableHeader = [Label.note, Label.createDate, Label.updateDate, Label.action];
        temp.actionBtns = this.actionBtns;
        temp.btnLink = this.actionLinks;
        temp.objectLabel = this.objectLabel;
        this.reportLists = temp;
        this.isLoading = false;
        this.searchData = null;
      })
  }

  onFilter(data: any) {
    this.filter = data;
    if (this.type == 'progress')
      this.getReportProgress();
    else this.getReportStudents();
  }

  search(search: String) {
    this.searchData = search;
    if (this.type == 'progress')
      this.getReportProgress();
    else this.getReportStudents();
  }

  goTocreate() {
    this._router.navigateByUrl("teacher/reports/students/create/" + this.studentId)
  }

  pageChanges(e: any) {
    console.log(e);
    this.page = e;
    if (this.type == 'progress')
      this.getReportProgress();
    else this.getReportStudents();
  }

}
